import Button from "@material-ui/core/Button";
import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import InputBase from "@material-ui/core/InputBase";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import SearchIcon from "@material-ui/icons/Search";
import { withStyles } from "@material-ui/styles";
import Axios from "axios";
import React from "react";
import { Link } from "react-router-dom";


const styles = theme => ({
  root: {
    width: "100%",
    overflowX: "auto"
  },
  table: {
    // minWidth: 650
  },
  searchIcon: {
    // width: theme.spacing(7),
    width: 7,
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  inputRoot: {
    color: "inherit"
  },
  inputInput: {
    // padding: theme.spacing(1, 1, 1, 7),
    padding: 7,
    // transition: theme.transitions.create('width'),
    width: "100%"
    // [theme.breakpoints.up('md')]: {
    //   width: 200,
    // },
  },
  search: {
    position: "relative",
    // borderRadius: theme.shape.borderRadius,
    // backgroundColor: fade(theme.palette.common.white, 0.15),
    // "&:hover": {
    //     backgroundColor: fade(theme.palette.common.white, 0.25),
    // },
    // marginRight: theme.spacing(2),
    marginLeft: 0,
    width: "100%"
    // [theme.breakpoints.up('sm')]: {
    //   marginLeft: theme.spacing(3),
    //   width: 'auto',
    // },
  },
  rootSearch: {
    padding: "2px 16px",
    display: "flex",
    alignItems: "center",
    width: 400,
    marginBottom: 24,
    paddingLeft: "36px"
  },
  input: {
    flex: 1
  },
  iconButton: {
    padding: 16
  }
});

class SearchCertPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {},
      searchID: "",
      rows: [],
      isLoading: false
    };
  }
  componentDidMount() {}

  onSubmitSearch = () => {
    this.setState({ isLoading: true, rows: [] });
    // console.log(this.state.searchID);
    Axios.get(`http://localhost:8081/api/v1/certs/search/${this.state.searchID}`)
      .then(res => {
        this.setState({ rows: res.data, isLoading: false });
        console.log(this.state.rows);
      })
      .catch(e => this.setState({ isLoading: false }));
  };

  handleChange = event => {
    // this.setState({ searchID: event.target.value });
    this.setState({
      searchID: event.target.value
    });
  };

  render() {
    // const { errors } = this.state;
    const { classes } = this.props;

    return (
      <>
        <Grid
          container
          direction="column"
          style={{ height: "100%", backgroundColor: "whitesmoke" }}
        >
          <Grid className="HeaderLeftHome" item>
            <Grid
              container
              className="header-homepage"
              style={{ backgroundColor: "white" }}
            >
              <Grid
                className="HeaderLeftHome"
                item
                xs={12}
                md={4}
                sm={4}
                style={{ marginBottom: 0 }}
              >
                <div className="logo logo-homepage">
                  <Link to="/profile">
                    <div
                      style={{
                        height: "56px",
                        margin: 16,
                        background: `url("/logo.png") no-repeat left center`,
                        backgroundSize: "contain"
                      }}
                    ></div>
                  </Link>
                </div>
              </Grid>
              <Grid
                className="HeaderRightHome"
                item
                xs={12}
                md={8}
                sm={8}
                container
              >
                <Grid item>
                  <Link
                    to="/searchcert"
                    style={{ textDecoration: "none", marginLeft: "30px" }}
                  >
                    <Button
                      style={{
                        background: "#2b2b2b",
                        color: "white",
                        fontWeight: 600,
                        boxShadow: "none",
                        fontSize: "16px",
                        paddingLeft: "20px",
                        paddingRight: "20px"

                        // width: "155px",
                      }}
                      startIcon={<SearchIcon />}
                      variant="contained"
                    >
                      Tra Chứng Chỉ
                    </Button>
                  </Link>
                </Grid>

                <Grid item>
                  <Link
                    to="/login"
                    style={{ textDecoration: "none", marginLeft: "30px" }}
                  >
                    <Button
                      style={{
                        background: "#2196f3",
                        color: "white",
                        fontWeight: 600,
                        fontSize: "16px",
                        boxShadow: "none",
                        width: "125px"
                      }}
                      variant="contained"
                    >
                      Đăng nhập
                    </Button>
                  </Link>
                </Grid>
                <Grid item>
                  <Link
                    to="/signup"
                    style={{ textDecoration: "none", marginLeft: "30px" }}
                  >
                    <Button
                      style={{
                        background: "#7bccd5",
                        color: "white",
                        fontWeight: 600,
                        boxShadow: "none",
                        fontSize: "16px",
                        width: "125px"
                      }}
                      variant="contained"
                    >
                      Đăng ký
                    </Button>
                  </Link>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid className="HeaderLeftHome" item>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
              style={{ height: "100%", padding: "64px" }}
            >
              <Paper
                onSubmit={this.onSubmitSearch}
                component="form"
                className={classes.rootSearch}
              >
                <InputBase
                  className={classes.input}
                  placeholder="Mã Chứng Chỉ"
                  inputProps={{ "aria-label": "mã chứng chỉ" }}
                  onChange={this.handleChange}
                />
                <IconButton
                  //   type="submit"
                  onClick={this.onSubmitSearch}
                  className={classes.iconButton}
                  aria-label="search"
                >
                  <SearchIcon />
                </IconButton>
              </Paper>

              <Paper className={classes.root}>
                <Table className={classes.table} aria-label="simple table">
                  <TableHead>
                    <TableRow>
                      <TableCell >ID</TableCell>
                      <TableCell align="right">Trạng thái</TableCell>
                      <TableCell align="right">Thời gian cập nhập</TableCell>
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    {this.state.rows.map(row => (
                      <TableRow key={row.id}>
                        <TableCell component="th" scope="row" >
                          {row.search_id}
                        </TableCell>
                        <TableCell align="right">{row.status}</TableCell>
                        <TableCell align="right">{Date(row.timestamp).toLocaleString("vi")}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>

                {this.state.isLoading ? (
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      height: "200px",
                      alignItems: "center",
                      flexDirection: "column",
                      margin: "32px"
                    }}
                  >
                    <CircularProgress size={64} style={{ margin: "32px" }} />
                  </div>
                ) : (
                  <> </>
                )}
                {this.state.rows.length === 0 && !this.state.isLoading ? (
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      height: "200px",
                      alignItems: "center",
                      flexDirection: "column",
                      margin: "32px"
                    }}
                  >
                    <svg
                      width="184"
                      height="152"
                      viewBox="0 0 184 152"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <g fill="none" fillRule="evenodd">
                        <g transform="translate(24 31.67)">
                          <ellipse
                            fillOpacity=".8"
                            fill="#F5F5F7"
                            cx="67.797"
                            cy="106.89"
                            rx="67.797"
                            ry="12.668"
                          ></ellipse>
                          <path
                            d="M122.034 69.674L98.109 40.229c-1.148-1.386-2.826-2.225-4.593-2.225h-51.44c-1.766 0-3.444.839-4.592 2.225L13.56 69.674v15.383h108.475V69.674z"
                            fill="#AEB8C2"
                          ></path>
                          <path
                            d="M101.537 86.214L80.63 61.102c-1.001-1.207-2.507-1.867-4.048-1.867H31.724c-1.54 0-3.047.66-4.048 1.867L6.769 86.214v13.792h94.768V86.214z"
                            fill="url(#linearGradient-1)"
                            transform="translate(13.56)"
                          ></path>
                          <path
                            d="M33.83 0h67.933a4 4 0 0 1 4 4v93.344a4 4 0 0 1-4 4H33.83a4 4 0 0 1-4-4V4a4 4 0 0 1 4-4z"
                            fill="#F5F5F7"
                          ></path>
                          <path
                            d="M42.678 9.953h50.237a2 2 0 0 1 2 2V36.91a2 2 0 0 1-2 2H42.678a2 2 0 0 1-2-2V11.953a2 2 0 0 1 2-2zM42.94 49.767h49.713a2.262 2.262 0 1 1 0 4.524H42.94a2.262 2.262 0 0 1 0-4.524zM42.94 61.53h49.713a2.262 2.262 0 1 1 0 4.525H42.94a2.262 2.262 0 0 1 0-4.525zM121.813 105.032c-.775 3.071-3.497 5.36-6.735 5.36H20.515c-3.238 0-5.96-2.29-6.734-5.36a7.309 7.309 0 0 1-.222-1.79V69.675h26.318c2.907 0 5.25 2.448 5.25 5.42v.04c0 2.971 2.37 5.37 5.277 5.37h34.785c2.907 0 5.277-2.421 5.277-5.393V75.1c0-2.972 2.343-5.426 5.25-5.426h26.318v33.569c0 .617-.077 1.216-.221 1.789z"
                            fill="#DCE0E6"
                          ></path>
                        </g>
                        <path
                          d="M149.121 33.292l-6.83 2.65a1 1 0 0 1-1.317-1.23l1.937-6.207c-2.589-2.944-4.109-6.534-4.109-10.408C138.802 8.102 148.92 0 161.402 0 173.881 0 184 8.102 184 18.097c0 9.995-10.118 18.097-22.599 18.097-4.528 0-8.744-1.066-12.28-2.902z"
                          fill="#DCE0E6"
                        ></path>
                        <g transform="translate(149.65 15.383)" fill="#FFF">
                          <ellipse
                            cx="20.654"
                            cy="3.167"
                            rx="2.849"
                            ry="2.815"
                          ></ellipse>
                          <path d="M5.698 5.63H0L2.898.704zM9.259.704h4.985V5.63H9.259z"></path>
                        </g>
                      </g>
                    </svg>
                    <p>No Data</p>
                  </div>
                ) : (
                  <></>
                )}
              </Paper>
            </Grid>
          </Grid>
        </Grid>
      </>
    );
  }
}

export default withStyles(styles, { withTheme: true })(SearchCertPage);
